rdion.scss\n//\n//\n\n\n// Markup:\n//\n// .accordion > .accordion-item > [data-toggle=\"collapse\"], .collapse\n\n.accordion{\n  // Ensure accordions are spaced away from adjacent siblings\n  &:not(:first-child){\n    margin-top: $spacer*2;\n  }\n  &:not(:last-child){\n    margin-bottom: $spacer;\n  }\n}\n\n.accordion-item{\n  > [data-toggle=\"collapse\"]{\n    transition: margin .35s ease, opacity .35s ease;\n    cursor: pointer;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-between;\n    > *{\n      margin-bottom: 0;\n    }\n    &:hover{\n      text-decoration: none;\n    }\n    i{\n      transition: transform .35s ease;\n    }\n    // Rotate arrow icon when item is open\n    &[aria-expanded=\"true\"]{\n      i{\n        transform: rotate(90deg);\n      }\n      margin-bottom: $spacer/2;\n    }\n    // Fade accordion title when not active or hovered\n    &[aria-expanded=\"false\"]{\n      opacity: .5;\n      &:hover{\n        opacity: 1;\n      }\n    }\n  }\n  // Add border to separate items\n  &:not(:first-child){\n    border-top: $hr-border-width solid $hr-border-color;\n  }\n  padding: $spacer/2 0;\n}\n\n// Color considerations:\n//\n// When accordions are used on dark backgrounds\n\n.bg-dark{\n  .accordion-item{\n    &:not(:first-child){\n      border-top: $hr-border-width solid $gray-800;\n    }\n  }\n}\n","// \n// \n// article.scss\n//\n//\n\narticle{\n  p + p, p + ul, ul + p{\n    margin-top: $spacer;\n  }\n  h4,h5{\n    margin-bottom: $spacer;\n    &:not(:first-child){\n      margin-top: $spacer*2;\n    }\n  }\n  .blockquote{\n    margin: $spacer*2 0;\n    .h4{\n      margin-bottom: $spacer/1.5;\n    }\n    &:first-child{\n      margin-top: 0;\n    }\n  }\n  figure{\n    margin: $spacer*2 0;\n    &:first-child{\n      margin-top: 0;\n    }\n  }\n  h5 + figure{\n    margin-top: 0;\n  }\n  ul{\n    padding-left: $spacer*2;\n    margin-bottom: $spacer;\n    > li{\n      margin-bottom: .5rem;\n    }\n  }\n  + article{\n    margin-top: