");\n    }\n  }\n}\n","// \n// \n// thumbnail.scss\n//\n// A set of shortcut classes for restricting the height of images to thumbnail appropriate sizes\n\n\n.thumbnail{\n  display: inline-block;\n  text-align: center;\n  border-radius: $border-radius/2;\n  width: $spacer*3;\n  &.thumbnail-sm{\n    width: $spacer*2;\n  }\n  + span{\n    display: inline-block;\n    margin-left: $spacer/2;\n  }\n}","// \n// \n// tables.scss\n//\n//\n\n\n// Bootstrap overrides\n//\n// General overrides for default table styles\n\nthead{\n  tr{\n    th{\n      font-weight: $font-weight-normal;\n      color: $headings-color;\n    }\n  }\n}\n\ntbody{\n  tr{\n    th{\n      font-weight: $font-weight-normal;\n    }\n  }\n}\n\n.table{\n  thead{\n    th{\n      border-bottom-width: 1px;\n    }\n  }\n  &.align-items-center{\n    th,td{\n      vertical-align: middle;\n    }\n  }\n}\n\n.table-hover tr{\n  transition: background-color .2s ease;\n}\n\n// Additional table styles\n//\n//\n\n.table-borderless tr.bg-white{\n  > th, > td{\n    padding: 1rem;\n    position: relative;\n    &:after{\n      content: '';\n      position: absolute;\n      width: 100%;\n      height: 100%;\n      border-top: 1px solid $border-color;\n      border-bottom: 1px solid $border-color;\n      left: 0;\n      top: 0;\n      pointer-events: none;\n    }\n  }\n  > th{\n    border-radius: $border-radius 0 0 $border-radius;\n    &:after{\n      border-radius: $border-radius 0 0 $border-radius;\n      border-left: 1px solid $border-color;\n    }\n  }\n  > td:last-child{\n    border-radius: 0 $border-radius $border-radius 0;\n    &:after{\n      border-radius: 0 $border-radius $border-radius 0;\n      border-right: 1px solid $border-color;\n    }\n  }\n}\n\n.table-hover tr.bg-white:hover{\n  background-color: $gray-200 !important;\n}\n\ntr.table-divider{\n  height: $spacer/2;\n  pointer-events: none;\n  th, td{\n    padding: 0 !important;\n  }\n}","// \n// \n// titles.scss\n//\n//\n\n\n.section-intro{\n  margin-bottom: $spacer;\n}\n\n@include media-breakpoint-up(sm) {\n  .section-intro{\n    margin-bottom: $spacer*2;\n  }\n}\n\n.row.align-items-center{\n  .section-intro{\n    margin-bottom: 0;\n  }\n}\n\n@include media-breakpoint-down(sm) {\n  .row.align-items-center{\n    .section-intro{\n      margin-bottom: $spacer;\n    }\n  }\n}\n\n.section-outro{\n  margin-top: $spacer;\n}\n\n@include media-breakpoint-up(sm) {\n  .section-outro{\n    margin-top: $spacer*2;\n  } \n}\n\n.title-decorative{\n  font-size: 0.875rem;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n  color: $gray-500;\n  margin-bottom: $spacer/2;\n  display: block;\n  font-weight: 500;\n  &:last-child{\n    margin-bottom: 0;\n  }\n}\n\n@include media-breakpoint-up(sm) {\n  .title-decorative{\n    margin-bottom: $spacer;\n  }\n}","// \n// \n// video.scss\n//\n//\n\n\n// Bootstrap overrides\n//\n// General overrides for default embed styles\n\n.embed-responsive{\n  background: $black;\n}\n\n// Video covers\n//\n// Element that allows embeddable media inside while keeping theme consistens styling\n\n$video-cover-transition: .35s;\n\n@keyframes fadeOut{\n  0%{ opacity: 1; }\n  100%{ opacity: 0; transform: scale(1.5); }\n}\n\n.video-cover{\n  background: $black;\n  position: relative;\n  overflow: hidden;\n  .bg-image{\n    z-index: 1;\n    transition: opacity $video-cover-transition ease;\n  }\n  .video-play-icon{\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    z-index: 2;\n    margin-top: -36px;\n    margin-left: -36px;\n    margin-bottom: 0 !important;\n  }\n  iframe{\n    background: $black;\n    transition: opacity $video-cover-transition ease .35s;\n    opacity: 0;\n  }\n  &.video-cover-playing{\n    .bg-image{\n      opacity: 0;\n      pointer-events: none;\n    }\n    .video-play-icon{\n      animation: fadeOut .35s ease forwards;\n      pointer-events: none;\n    }\n    iframe{\n      opacity: 1;\n    }\n  }\n  &:not(:last-child){\n    margin-bottom: $spacer;\n  }\n}\n\n.media{\n  > .video-cover